import { Component, OnInit } from '@angular/core';
import { FirebaseService } from '../services/firebase.service';
import { Observable } from 'rxjs/Observable';
import { MatSnackBar } from '@angular/material';
import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-comments',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.css']
})
export class CommentsComponent implements OnInit {

  commentsList: Observable<any[]>;

  constructor(
    private firebaseS: FirebaseService,
    public snackBar: MatSnackBar,
    private auth: AuthService
  ) { }

  ngOnInit() {
    this.commentsList = this.firebaseS.getList('/comentarios', 'fecha').map(val => {
      val.sort((a: any, b: any) => new Date(a.fecha).getTime() - new Date(b.fecha).getTime());
      return val;
    })
  }

  comentar(comment) {
    if(this.auth.isLoggedIn()) {
      let fecha = new Date().getTime();
      let name = this.auth.getUser().displayName + ' ' + fecha;
      let obj = {
        comentario: comment,
        fecha: fecha,
        nombre: this.auth.getUser().displayName
      }
      this.firebaseS.setInList('/comentarios', name, obj);
    } else {
      this.snackBar.open('Debes ingresar primero', 'Ok', {
        duration: 2000,
      });
    }
  }

}



