import { Component, OnInit, Input } from '@angular/core';
import { MatSnackBar } from '@angular/material';
import { AngularFireDatabase, AngularFireObject } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-book-selected',
  templateUrl: './book-selected.component.html',
  styleUrls: ['./book-selected.component.css']
})
export class BookSelectedComponent implements OnInit {

  @Input() book;
  booksList: AngularFireObject<any>;
  turnos: Array<any> = [];
  updateOrNot: boolean = false;

  constructor(
    public snackBar: MatSnackBar,
    private db: AngularFireDatabase,
    private auth: AuthService
  ) { }

  ngOnInit() {
    this.booksList = this.db.object('/books');
  }

  seleccionar() {
    if(this.auth.isLoggedIn()) {
      this.setData();
    } else {
      this.snackBar.open('Debes ingresar primero', 'Ok', {
        duration: 2000,
      });
    }
  }

  setData() {
    let currentUser = this.auth.getUser();
    let indice;
    let name = currentUser.displayName;
    this.booksList.snapshotChanges().take(1).subscribe(action => {
      let values = action.payload.val();
      if(values) {
        let objKeys = Object.keys(values);
        indice = objKeys.findIndex(val => currentUser.displayName === val);
      } else {
        indice = -1;
      }
      if(indice === -1) {
        let obj = {
          autor: this.book.authors[0],
          nombre: name,
          title: this.book.title,
          orden: '#'
        }
        this.db.list('/books').set(name, obj).then(
          val => {
            this.sortea();
          }
        ).catch(
          err => {
            console.log('Error', err);
          }
        )
      } else {
        this.snackBar.open('Ya escogiste un libro', 'Ok', {
          duration: 2000,
        });
      }

    });
  }

  sortea() {
    this.turnos = [];
    this.booksList.snapshotChanges().take(1).subscribe(action => {
      let values = action.payload.val();
      let objKeys = Object.keys(values);
      objKeys.forEach(val => {
        let turno = this.getTurno(objKeys.length);
        this.turnos.push(turno);
        values[val].orden = turno;
      });
      this.update(values);
    });
  }

  update(values) {
    let path = '/';
    let objToUpdate = this.db.list(path);
    objToUpdate.set('books', values);
  }

  getTurno(cantidad) {
    let turno;
    let findIndex;
    do {
      turno = Math.floor(Math.random() * cantidad) + 1;
      findIndex = this.turnos.findIndex(val => val === turno);
    }
    while (findIndex != -1);
    return turno;
  }


}



