import { Injectable } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class FirebaseService {

  constructor(
    private db: AngularFireDatabase
  ) { }

  getList(listPath: string, orderBy: string): Observable<any[]> {
    return this.db.list(listPath, ref => ref.orderByChild('orden')).valueChanges();
  }

  setInList(listPath: string, name: string, obj) {
    this.db.list(listPath).set(name, obj).then(
      () => {
        console.log('Guardado');
      }
    ).catch(
      err => {
        console.log('Error', err);
      }
    )
  }

}



