import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, AngularFireObject } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-sortear',
  templateUrl: './sortear.component.html',
  styleUrls: ['./sortear.component.css']
})
export class SortearComponent implements OnInit {

  booksList: AngularFireObject<any>;
  turnos: Array<any> = [];

  constructor(
    private db: AngularFireDatabase
  ) { }

  ngOnInit() {
    this.booksList = this.db.object('/books');
  }

  sortea() {
    this.turnos = [];
    this.booksList.snapshotChanges().take(1).subscribe(action => {
      let values = action.payload.val();
      let objKeys = Object.keys(values);
      objKeys.forEach(val => {
        let turno = this.getTurno(objKeys.length);
        this.turnos.push(turno);
        values[val].orden = turno;
      });
      this.update(values);
    });
  }

  update(values) {
    let path = '/';
    let objToUpdate = this.db.list(path);
    objToUpdate.set('books', values);
  }

  getTurno(cantidad) {
    let turno;
    let findIndex;
    do {
      turno = Math.floor(Math.random() * cantidad) + 1;
      findIndex = this.turnos.findIndex(val => val === turno);
    }
    while (findIndex != -1);
    return turno;
  }

}



