import { Component, OnInit, Output, EventEmitter, HostListener, ViewChild, ElementRef } from '@angular/core';
import { HttpRequestsService } from '../services/http-requests.service';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {

  @Output() dataToEmit: EventEmitter<any> = new EventEmitter<any>();
  loading: boolean = false;
  titleOrAuthor: string = 'título';
  @ViewChild('book') private bookEle : ElementRef;
  @HostListener('window:keydown', ['$event'])
  keyEvent(e: KeyboardEvent) {
    if(e.code === "Enter") {
      if(this.bookEle.nativeElement.value !== '') {
        this.busca(this.bookEle.nativeElement.value);
      }
    }
  }

  constructor(
    private http: HttpRequestsService
  ) { }

  ngOnInit() {
  }

  busca(book) {
    this.loading = true;
    let type: string = '';
    if(this.titleOrAuthor === 'título') {
      type = 'title';
    } else {
      type = 'author';
    }
    let url = 'https://infinite-journey-77948.herokuapp.com/api';
    let params = { book: book, type: type };
    let sus = this.http.getRequest(url, params).first().subscribe(
      res => {
        this.dataToEmit.emit(res);
        this.loading = false;
      },
      err => {
        console.log('Error', err);
      }
    );
  }

}



