import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  data;
  selected;

  constructor(

  ) { }

  ngOnInit() {
  }

  getData(e) {
    this.data = e;
  }

  bookSelection(e) {
    this.selected = e;
  }

}



